<template>
    <div class="main-page" :style="{background: `url(${appStyle.FBackGround}) no-repeat `}">
        <div>
            <van-swipe class="my-swipe" :autoplay="3000" indicator-color="white">
                <van-swipe-item v-for="(image, index) in swipeList" :key="index">
                    <van-image :src="image" />
                </van-swipe-item>
            </van-swipe>
            <div v-for="(item,index) in lists" :key="index" class="list-item" @click="toDetails(item)">
                <img :src="item.CoverImg" />
                <div class="item-bottom">
                    <div class="item-botton-left">
                        <div class="hidden-one">{{item.Name}}</div>
                        <div class="item-bottom-time">{{$time(item.StartTime).format("YYYY-MM-DD")}} 至 {{$time(item.EndTime).format("YYYY-MM-DD")}}</div>
                    </div>
                    <div class="item-bottom-btn">立即参与</div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "QsmsList",
    props: {},
    components: {},
    data() {
        return {
            appStyle: {},
            swipeList: [],
            lists: []
        }
    },
    methods: {
        getAppStyle() {
            return this._api.BOS_AppStyleDetail({ FNumber: "qsms_list" }).then(res => {
                if (res) {
                    this.appStyle = res.data
                    this.swipeList = JSON.parse(res.data.FSlideImges)
                }
            })
        },
        QSMS_QuestionnaireIndex() {
            this._api.QSMS_QuestionnaireIndex().then(res => {
                if (res) {
                    this.lists = res.data
                }
            })
        },

        // <el-option :value="101" label="问卷"></el-option>
        //                     <el-option :value="201" label="集卡"></el-option>
        //                     <el-option :value="301" label="投票"></el-option>
        //  <el-option :value="901" label="其他"></el-option>

        toDetails(item) {
            let obj = {
                101: `/QsmsDetails101?QuestionnaireId=${item.Id}`,
                301: `/QsmsDetails101?QuestionnaireId=${item.Id}`,
                801: `/QsmsCard?QuestionnaireId=${item.Id}`
            }
            this.$router.push(obj[item.Model] ? obj[item.Model] : `/QsmsCard?QuestionnaireId=${item.Id}`)
        }
    },
    async mounted() {
        await this.getAppStyle()
        this.QSMS_QuestionnaireIndex()
    },
}
</script>

<style lang="scss" scoped>
.main-page {
    background-size: cover !important;
    overflow: auto;
    padding-bottom: 20px;

    .van-image {
        width: 100%;
    }

    .list-item {
        width: calc(100% - 30px);
        height: 235px;
        margin: 5px 15px 10px;
        border: 3px;
        overflow: hidden;
        background: rgba($color: #fff, $alpha: 0.2);
        padding: 6px;

        img {
            height: 180px;
            width: 100%;
            object-fit: cover;
            border-radius: 2px;
        }

        .item-bottom {
            display: flex;
            align-items: center;
            justify-content: space-between;
            color: #fff;

            .item-botton-left {
                width: calc(100% - 90px);

                .item-bottom-time {
                    color: #f3c1bc;
                    font-size: 14px;
                }
            }

            .item-bottom-btn {
                width: 90px;
                background: #cf1808;
                border-radius: 3px;
                padding: 6px 15px;
                font-size: 14px;
            }
        }
    }
}
</style>